import * as React from "react";
import { graphql, useStaticQuery } from "gatsby";
import {Helmet} from "react-helmet";
import { scroller } from "react-scroll";
import 'bootstrap/dist/css/bootstrap.min.css';
import * as $ from "jquery";
import Navbar from '../components/navbar';
import Carousel from '../components/carousel';
import Overview from '../components/overview';
import Highlights from '../components/highlights';
import Amenities from '../components/amenities';
import Location from '../components/location';
import Plans from '../components/plans';
import Price from '../components/price';
import Gallery from '../components/gallery';
import Video from '../components/video';
import Contact from '../components/contact';
import Footer from '../components/footer';
import MainForm from '../components/mainForm';
import FixedFooter from '../components/fixedFooter';
import Modal from '../components/Modal';
import "/src/css/custom.css";
import  { useCookies }  from 'react-cookie';
import { fn } from "jquery";

// styles
const pageStyles = {
  color: "#232129",
  fontFamily: "-apple-system, Roboto, sans-serif, serif",
}

let i = 1

// markup
const IndexPage = () => {

  const [cookies, setCookie] = useCookies(['logo']);

  // ----- Onload GraphQL Query Start -----//
  const data = useStaticQuery(graphql`
    query MyQuery {
      wpProject {
        projectMeta {
          projectId
          project
        }
      }
      wpLogo {
        logoMeta {
          logo {
            sourceUrl
          }
        }
      }
      allWpBanner(
        sort: {fields: bannerMeta___order}
        filter: {status: {eq: "publish"}}
      ) {
        edges {
          node {
            bannerMeta {
              imageUpload {
                sourceUrl
              }
            }
          }
        }
      }
      wpOverview {
        overviewMeta {
          lessDescription
          moreDescription
        }
      }
      allWpHighlight(
        sort: {fields: projectHighlightMeta___order}
        filter: {status: {eq: "publish"}}
      ) {
        edges {
          node {
            projectHighlightMeta {
              description
              image {
                sourceUrl
              }
            }
          }
        }
      }
      allWpAmenitiesTab(
        sort: {fields: amenitiesTabsMeta___order, order: ASC}
        filter: {status: {eq: "publish"}}
      ) {
        edges {
          node {
            amenitiesTabsMeta {
              title
            }
          }
        }
      }
      allWpAmenitiesIcon(
        sort: {fields: amenitiesIconsMeta___order}
        filter: {status: {eq: "publish"}}
      ) {
        edges {
          node {
            amenitiesIconsMeta {
              icon {
                sourceUrl
              }
              title
              tabsTitle
            }
          }
        }
      }
      allWpLocationContent(
        sort: {fields: locationContentMeta___orderNo}
        filter: {status: {eq: "publish"}}
      ) {
        edges {
          node {
            locationContentMeta {
              title
              pointers
            }
          }
        }
      }
      allWpLocationImage(
        sort: {fields: locationImagesMeta___order}
        filter: {status: {eq: "publish"}}
      ) {
        edges {
          node {
            locationImagesMeta {
              image {
                sourceUrl
              }
            }
          }
        }
      }
      allWpProjectTab(
        sort: {fields: projectTabMeta___order}
        filter: {status: {eq: "publish"}}
      ) {
        edges {
          node {
            projectTabMeta {
              title
            }
          }
        }
      }
      allWpProjectImage(
        sort: {fields: projectImagesMeta___order}
        filter: {status: {eq: "publish"}}
      ) {
        edges {
          node {
            projectImagesMeta {
              title
              tabTitle
              image {
                sourceUrl
              }
            }
          }
        }
      }
      allWpPrice(
        sort: {fields: priceContentMeta___order}
        filter: {status: {eq: "publish"}}
      ) {
        edges {
          node {
            priceContentMeta {
              title
              area
              price
            }
          }
        }
      }
      allWpGalleryTab(
        sort: {fields: galleryTabMeta___order}
        filter: {status: {eq: "publish"}}
      ) {
        edges {
          node {
            galleryTabMeta {
              title
            }
          }
        }
      }
      allWpGalleryImage(
        sort: {fields: galleryImageMeta___order}
        filter: {status: {eq: "publish"}}
      ) {
        edges {
          node {
            galleryImageMeta {
              image {
                sourceUrl
              }
              tabsTitle
            }
          }
        }
      }
      wpContact {
        contactMeta {
          secondaryNumber
          primaryNumber
          mapLink
          email
          address
        }
      }
      wpFooter {
        footerMeta {
          reraNos
          disclaimer
          copyright
        }
      }
    }
  `)
  // ----- Onload GraphQL Query End -----//

  let project_id = data.wpProject.projectMeta.projectId
  React.useEffect(() => {
    localStorage.setItem('project_id', project_id);
  }, [project_id]);

  let project = data.wpProject.projectMeta.project
  React.useEffect(() => {
    localStorage.setItem('project', project);
  }, [project]);

  // ----- Logo -----//
  let logo = data.wpLogo.logoMeta.logo.sourceUrl
  React.useEffect(() => {
    setCookie('logo', logo, { path: '/' });
    localStorage.setItem('logo', logo);
  }, [logo]);

  // ----- Slider -----//
  let slider = data.allWpBanner.edges

  // ----- Overview -----//
  let overview = data.wpOverview.overviewMeta

  // ----- Highlight -----//
  let HighlightData = data.allWpHighlight.edges

  // ----- Amenities -----//
  let AmenitiesTabData = data.allWpAmenitiesTab.edges
  let AmenitiesIconData = data.allWpAmenitiesIcon.edges

  // ----- Location -----//
  let LocationContentData = data.allWpLocationContent.edges
  let LocationImageData = data.allWpLocationImage.edges

  // ----- Plans -----//
  let PlansTabData = data.allWpProjectTab.edges
  let PlansImageData = data.allWpProjectImage.edges

  // ----- Price -----//
  let PriceData = data.allWpPrice.edges

  // ----- Gallery -----//
  let GalleryTabData = data.allWpGalleryTab.edges
  let GalleryImageData = data.allWpGalleryImage.edges

  // ----- Contact -----//
  let contact = data.wpContact.contactMeta
  React.useEffect(() => {
    localStorage.setItem('contact', contact);
  }, [contact]);

  // ----- Footer -----//
  let rerano = data.wpFooter.footerMeta.reraNos
  let disclaimer = data.wpFooter.footerMeta.disclaimer
  let copyright = data.wpFooter.footerMeta.copyright

  React.useEffect(() => {
    localStorage.setItem('rerano', rerano);
  }, [rerano]);

  React.useEffect(() => {
    localStorage.setItem('disclaimer', disclaimer);
  }, [disclaimer]);

  React.useEffect(() => {
    localStorage.setItem('copyright', copyright);
  }, [copyright]);

  React.useEffect(() => {
    if(localStorage.getItem('scrollToSection')){
      var section = localStorage.getItem('scrollToSection');
      localStorage.removeItem('scrollToSection')
      $('html, body').animate({
        scrollTop: $("#"+section).offset().top
    }, 1000,);
    }
    setTimeout(() => {
      $('#enquireModal').addClass("show");
      $('#enquireModal').css("display", "block");
      $('#enquireModal').prop("aria-hidden", "false");
      $("body").append('<div class="modal-backdrop fade show"></div>');
    }, 10000)
  }, [])

  const [offset, setOffset] = React.useState(0);

  React.useEffect(() => {
    const onScroll = () => setOffset(window.pageYOffset);
    // clean up code
    window.removeEventListener('scroll', onScroll);
    window.addEventListener('scroll', onScroll, { passive: true });
    return () => window.removeEventListener('scroll', onScroll);
  }, []);

  if (i == 1) {
    if (offset > 200) {
      if ($('.mainForm .accordion-collapse').hasClass('show')) {
        $(".mainForm .accordion-header button").trigger("click");
      }
      i++;
    }
  }
  else if (i == 2) {
    if (offset < 200) {
      $(".mainForm .accordion-header button").trigger("click");
      i--;
    }
  }

  return (
    <main style={pageStyles}>
      <title>Prime Park</title>

      <Helmet>
      <link rel='stylesheet' href='https://manishalifespaces.com/chat/build/chat.min.css?ver=12456321' type='text/css' media='all' />
      <script src="https://cdnjs.cloudflare.com/ajax/libs/jquery/3.6.0/jquery.min.js" integrity="sha512-894YE6QWD5I59HgZOGReFYm4dnWc1Qt5NtvYSaNcOP+u1T9qYdvdihz0PPSiiqn/+/3e7Jo4EaG7TubfWGUrMQ==" crossorigin="anonymous" referrerpolicy="no-referrer"></script>
<script type='text/javascript' async='async' src='https://manishalifespaces.com/chat/build/chat.min.js?ver=12456321' defer></script>
      </Helmet>

      <Navbar logo={logo} />
      <Carousel slider={slider} />
      <Overview overview={overview} />
      <Highlights highlights={HighlightData} />
      <Amenities amenitiesTabs={AmenitiesTabData} amenitiesIcons={AmenitiesIconData} />
      <Location locationContent={LocationContentData} locationImage={LocationImageData} />
      <Plans planTabs={PlansTabData} planImages={PlansImageData} />
      <Price price={PriceData} logo={logo} />
      <Gallery galleryTabs={GalleryTabData} galleryImages={GalleryImageData} />
      <Video />
      <Contact contact={contact} project_id={project_id} project={project} />
      <Footer rerano={rerano} disclaimer={disclaimer} copyright={copyright} />
      <MainForm project_id={project_id} project={project} />
      <FixedFooter />
      <Modal project_id={project_id} project={project} />
      <div id="chat-circle" class="btn btn-raised"> <div id="chat-overlay"></div></div><div class="chat-box"> <div class="chat-box-header"> <div class="chat-header-dp"></div><div class="chat-header-name">Neha Arora</div><span class="chat-box-toggle"><i class="material-icons">close</i></span> </div><div class="chat-box-body"> <div class="chat-box-overlay"> </div><div class="chat-logs"> <div id="mls-chat-bot"> <bot-ui></bot-ui> </div></div></div></div>

    </main>
    

  )

}

export default IndexPage
