import React, { Component } from 'react';
import { Container, Row, Image, Navbar, Nav } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import * as $ from "jquery";
import sectionHeaderIcon from '../images/deal.png';


class Overview extends React.Component {

    render() {

        // ----- Read Start -----//
        const fnRead = (type) => {
            
            if(type == "more"){
                $(".readmore span").addClass("d-none");
                $(".readless").removeClass("d-none");
            }else{
                $(".readmore span").removeClass("d-none");
                $(".readless").addClass("d-none");
            }
            
        }
        // ----- Read End -----//

        return (
            <>

                <Container fluid className="bg-white" id="overview">
                    <Row className="section-heading mb-4 mx-md-5 mx-4">
                        <img
                            className="d-block m-auto mb-2"
                            src={sectionHeaderIcon}
                        />
                        <h1 className="m-0">Overview</h1>
                    </Row>
                    <Row className="mx-md-5 mx-4 text-center">
                        <p className='readmore'>{this.props.overview.lessDescription}<span><a href='javascript:void(0);' onClick={() => fnRead("more")}>Read More</a></span></p>
                        <p className='readless d-none'>{this.props.overview.moreDescription}<span><a href='javascript:void(0);' onClick={() => fnRead("less")}>Read Less</a></span></p>
                    </Row>
                </Container>

            </>
        )
    }
}
export default Overview;