import React, { Component } from 'react';
import { Row, Image, Nav, Col, Container } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import * as $ from "jquery";


class FixedFooter extends React.Component {

    render() {

        const fnOpenModal = () => {
            $('#enquireModal').addClass("show");
            $('#enquireModal').css("display","block");
            $('#enquireModal').prop("aria-hidden","false");
            $("body").append('<div class="modal-backdrop fade show"></div>');
        }

        return (
            <>

            <Nav className="fixed-bottom navbar-light bg-dark d-block d-sm-none">
                <Container>
                    <Row>
                        <Col className='col-5 text-center'>
                            <a className='text-white text-decoration-none' href="tel:02268680097">CALL NOW</a>
                        </Col>
                        <Col className='col-2'>
                            <a href="/whatsapp" className='text-white'>
                                <Image className='w-100' src="https://ginfomedia.in/production/mls/gatsby/wp-content/themes/mpd_theme_6_multi_v_8/assets/images/whatsapp_animation.gif" alt="" />
                            </a>
                        </Col>
                        <Col className='col-5 text-center'>
                            <a className='text-white text-decoration-none' href='javascript:void(0);' onClick={() => fnOpenModal()}>ENQUIRE NOW</a>
                        </Col>
                    </Row>
                </Container>
            </Nav>

            </>
        )
    }
}
export default FixedFooter;