import React, { Component } from 'react';
import { Container, Row, Card, Col } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import sectionHeaderIcon from '../images/deal.png';
import MultiCarousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';

const responsive = {
    superLargeDesktop: {
        // the naming can be any, depends on you.
        breakpoint: { max: 4000, min: 3000 },
        items: 5
    },
    desktop: {
        breakpoint: { max: 3000, min: 1024 },
        items: 3
    },
    tablet: {
        breakpoint: { max: 1024, min: 464 },
        items: 2
    },
    mobile: {
        breakpoint: { max: 464, min: 0 },
        items: 1
    }
};

class Highlights extends React.Component {

    render() {

        // ----- Highlight Start -----//
        let HighlightData = this.props.highlights
        let Highlight = []
        HighlightData.forEach((val, index) => {
            Highlight.push(
                <div className="p-sm-2 p-md-3" key={index}>
                    <Card className="p-2">
                        <Card.Img variant="top" src={val.node.projectHighlightMeta.image.sourceUrl} />
                        <Card.Body>
                            <Card.Text>
                                {val.node.projectHighlightMeta.description}
                            </Card.Text>
                        </Card.Body>
                    </Card>
                </div>
            )
        });
        // ----- Highlight End -----//

        return (
            <>

                <Container fluid className="bg-light" id="highlights">
                    <Row className="section-heading mb-4 mx-md-5 mx-4">
                        <img
                            className="d-block m-auto mb-2"
                            src={sectionHeaderIcon}
                        />
                        <h1 className="m-0">Project Highlights</h1>
                    </Row>
                    <Row className="mx-md-5 mx-4 text-center">
                        <Col>
                            <MultiCarousel responsive={responsive}>
                                {Highlight}
                            </MultiCarousel>
                        </Col>
                    </Row>
                </Container>

            </>
        )
    }
}
export default Highlights;
