import React, { Component } from 'react';
import { Container, Row, Col, Form, Button } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import sectionHeaderIcon from '../images/deal.png';
import * as $ from "jquery";


class Contact extends React.Component {

    constructor(props) {
        super(props);
        this.keyPress = this.keyPress.bind(this);
     }

     keyPress(e){
         if(e.target.id === "contact_mobile_no"){
            if(/\D/g.test(e.target.value)) {
                e.target.value = e.target.value.replace(/\D/g,'');
            }
         }
         if(e.target.id === "contact_name"){
            if(/[0-9]/g.test(e.target.value)) {
                e.target.value = e.target.value.replace(/[0-9]/g,'');
            }
         }
     }

    render() {
        
        // ----- Lead Start -----//
        const fnSendMeDetail = (form) => {
            var name = $("#" + form + " input[name=name]").val();
            var email = $("#" + form + " input[name=email]").val();
            var mobile_no = $("#" + form + " input[name=mobile_no]").val();

            var email_pattern = /^\w+([-+.'][^\s]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/;
            var mobile_length = mobile_no.length;

            if (name == "") {
                $("#" + form + " .form_error").html("Please enter name");
                return false;
            } else {
                $("#" + form + " .form_error").html("");
            }

            if (mobile_no == "") {
                $("#" + form + " .form_error").html("Please enter mobile no.");
                return false;
            } else if (mobile_length != 10) {
                $("#" + form + " .form_error").html("Please enter valid mobile no.");
                return false;
            } else {
                $("#" + form + " .form_error").html("");
            }

            if (email == "") {
                $("#" + form + " .form_error").html("Please enter email");
                return false;
            }else if (!email_pattern.test(email)) {
                $("#" + form + " .form_error").html("Please enter valid email");
                return false;
            } else {
                $("#" + form + " .form_error").html("");
            }

            $('#' + form + ' button').prop('disabled', true);

            var lead = {
                name: name,
                email: email,
                mobile: mobile_no,
                project: this.props.project,
                project_id: this.props.project_id
            };

            $.ajax({
                type: "post",
                url: 'https://mls-crm.manishalifespaces.com/api/add_lead',
                data: lead,
                success: function (data, status, jqXHR) {
                    window.location.href = '/thanks';
                },
                error: function (jqXHR, status) {
                    $('#' + form + ' button').prop('disabled', false);
                }
            });
        }
        // ----- Lead End -----//

        return (
            <>

                <Container fluid className="bg-white" id="contact">
                    <Row className="section-heading mb-4 mx-md-5 mx-4">
                        <img
                            className="d-block m-auto mb-2"
                            src={sectionHeaderIcon}
                        />
                        <h1 className="m-0">Contact Us</h1>
                    </Row>

                    <Row className="mx-md-5 mx-4">
                        <Col xs={12} sm={12} md={12} lg={6} xl={6}>
                            <iframe
                                src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3966.521260322283!2d106.8195613507864!3d-6.194741395493371!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x2e69f5390917b759%3A0x6b45e67356080477!2sPT%20Kulkul%20Teknologi%20Internasional!5e0!3m2!1sen!2sid!4v1601138221085!5m2!1sen!2sid"
                                frameBorder="0"
                                allowFullScreen=""
                                aria-hidden="false"
                                tabIndex="0"
                            />
                        </Col>
                        <Col xs={12} sm={12} md={12} lg={6} xl={6}>
                            <Form id="contactForm">
                                <Row>
                                    <Col md={12}>
                                        <h6>ENQUIRE NOW</h6>
                                        <hr>
                                        </hr>
                                        <div class="form-group floating">
                                            <input type="text" class="form-control floating" name="name" id="contact_name" onKeyUp={this.keyPress} required />
                                            <label for="contact_name">Name</label>
                                        </div>
                                    </Col>
                                    <Col md={6}>
                                        <div class="form-group floating">
                                            <input type="text" class="form-control floating" name="mobile_no" id="contact_mobile_no" onKeyUp={this.keyPress} required />
                                            <label for="contact_mobile_no">Mobile No.</label>
                                        </div>
                                    </Col>
                                    <Col md={6}>
                                        <div class="form-group floating">
                                            <input type="text" class="form-control floating" name="email" id="contact_email" required />
                                            <label for="contact_email">Email</label>
                                        </div>
                                    </Col>
                                    <Col md={12}>
                                        <p class="form_error"></p>
                                        <Button variant="primary" type="button" onClick={() => fnSendMeDetail("contactForm")}>
                                            Submit
                                        </Button>
                                    </Col>
                                </Row>
                            </Form>
                            <h6 className="mt-4">CALL US ON</h6>
                            <hr>
                            </hr>
                            <a href='tel:02268680097'>{this.props.contact.primaryNumber}</a>
                            <h6 className="mt-4">ADDRESS</h6>
                            <hr>
                            </hr>
                            <p>{this.props.contact.address}</p>
                        </Col>
                    </Row>

                </Container>

            </>
            
        )
    }
}
export default Contact;