import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import { Container, Row, Card, Nav, Tab } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import sectionHeaderIcon from '../images/deal.png';
import MultiCarousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';
import Lightbox from 'react-image-lightbox';
import 'react-image-lightbox/style.css';

const responsive = {
    superLargeDesktop: {
        // the naming can be any, depends on you.
        breakpoint: { max: 4000, min: 3000 },
        items: 5
    },
    desktop: {
        breakpoint: { max: 3000, min: 1024 },
        items: 3
    },
    tablet: {
        breakpoint: { max: 1024, min: 464 },
        items: 2
    },
    mobile: {
        breakpoint: { max: 464, min: 0 },
        items: 1
    }
};

class Plans extends React.Component {
    constructor() {
        super();

        this.state = {
            photoIndex: 0,
            isOpen: false,
        };

        this.handler = this.handler.bind(this);
    }

    handler(e) {
        this.state({
            photoIndex: e.target.getAttribute("data-index"),
            isOpen: true,
        });
    }

    render() {
        const { photoIndex, isOpen } = this.state;

        // ----- Plans Start -----//
        let PlansTabData = this.props.planTabs
        let PlansImageData = this.props.planImages
        let images = []
        let PlansTab = []
        let PlansImage = []
        let PlansImagePanel = []
        let dataIndx = 0;

        PlansTabData.forEach((val, index) => {
            PlansTab.push(
                <Nav.Item key={index}>
                    <Nav.Link eventKey={index}>{val.node.projectTabMeta.title}</Nav.Link>
                </Nav.Item>
            )
            PlansImage = []
        PlansImageData.forEach((image,imageIndex) => {
             if (val.node.projectTabMeta.title == image.node.projectImagesMeta.tabTitle) {
                 images.push(image.node.projectImagesMeta.image.sourceUrl,)
                 PlansImage.push(
                     <div className="p-sm-2 p-md-3" key={imageIndex}>
                         <Card className="p-2">
                             <Card.Img src={image.node.projectImagesMeta.image.sourceUrl} data-index={dataIndx} onClick={(e) => this.setState({ photoIndex: e.target.getAttribute("data-index"), isOpen: true })}/>
                         </Card>
                     </div>
                 ) 
                 dataIndx++;
             }
         });
         PlansImagePanel.push(
             <Tab.Pane eventKey={index}>
                 <MultiCarousel responsive={responsive}>
                     {PlansImage}
                 </MultiCarousel>
             </Tab.Pane>
         )
        });
        // ----- Plans End -----//

        return (
            <>

                {isOpen && (
                    <Lightbox
                        mainSrc={images[photoIndex]}
                        nextSrc={images[(photoIndex + 1) % images.length]}
                        prevSrc={images[(photoIndex + images.length - 1) % images.length]}
                        onCloseRequest={() => this.setState({ isOpen: false })}
                        onMovePrevRequest={() =>
                            this.setState({
                                photoIndex: (photoIndex + images.length - 1) % images.length,
                            })
                        }
                        onMoveNextRequest={() =>
                            this.setState({
                                photoIndex: (photoIndex + 1) % images.length,
                            })
                        }
                    />
                )}

                <Container fluid className="bg-white" id="plans">
                    <Row className="section-heading mb-4 mx-md-5 mx-4">
                        <img
                            className="d-block m-auto mb-2"
                            src={sectionHeaderIcon}
                        />
                        <h1 className="m-0">Project Plans</h1>
                    </Row>
                    <Row className="mx-md-5 mx-4 text-center">
                        <Tab.Container defaultActiveKey="0">
                            <Nav variant="pills">
                                {PlansTab}
                            </Nav>
                            <Tab.Content className="pt-5">
                                {PlansImagePanel}
                            </Tab.Content>
                        </Tab.Container>
                    </Row>
                </Container>

            </>
        )
    }
}
export default Plans;
