import React, { Component } from 'react';
import { Button, Form, Image, Navbar, Nav } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import * as $ from "jquery";

const closeBtn = {
  right: "15px",
  position: "absolute",
}

class Modal extends React.Component {

  constructor(props) {
    super(props);
    this.keyPress = this.keyPress.bind(this);
 }
keyPress(e){
   if(e.target.id === "mobile_no"){
      if(/\D/g.test(e.target.value)) {
          e.target.value = e.target.value.replace(/\D/g,'');
      }
   }
   if(e.target.id === "name"){
      if(/[0-9]/g.test(e.target.value)) {
          e.target.value = e.target.value.replace(/[0-9]/g,'');
      }
   }
}

  render() {

    // ----- Lead Start -----//
  const fnSendMeDetail = (form) => {
    var name = $("#" + form + " input[name=name]").val();
    var email = $("#" + form + " input[name=email]").val();
    var mobile_no = $("#" + form + " input[name=mobile_no]").val();

    var email_pattern = /^\w+([-+.'][^\s]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/;
    var mobile_length = mobile_no.length;

    if (name == "") {
        $("#" + form + " .form_error").html("Please enter name");
        return false;
    } else {
        $("#" + form + " .form_error").html("");
    }

    if (mobile_no == "") {
        $("#" + form + " .form_error").html("Please enter mobile no.");
        return false;
    } else if (mobile_length != 10) {
        $("#" + form + " .form_error").html("Please enter valid mobile no.");
        return false;
    } else {
        $("#" + form + " .form_error").html("");
    }

    if (email == "") {
        $("#" + form + " .form_error").html("Please enter email");
        return false;
    }else if (!email_pattern.test(email)) {
        $("#" + form + " .form_error").html("Please enter valid email");
        return false;
    } else {
        $("#" + form + " .form_error").html("");
    }

    $('#' + form + ' button').prop('disabled', true);

    var lead = {
        name: name,
        email: email,
        mobile: mobile_no,
        project: this.props.project,
        project_id: this.props.project_id
    };

    $.ajax({
        type: "post",
        url: 'https://mls-crm.manishalifespaces.com/api/add_lead',
        data: lead,
        success: function (data, status, jqXHR) {
            window.location.href = '/thanks';
        },
        error: function (jqXHR, status) {
            $('#' + form + ' button').prop('disabled', false);
        }
    });
}
// ----- Lead End -----//

const fnCloseModal = () => {
  $('#enquireModal').removeClass("show");
  $('#enquireModal').css("display", "none");
  $('#enquireModal').prop("aria-hidden", "true");
  $(".modal-backdrop").remove();
}

    return (
      <>

        <div className="modal fade" id="enquireModal" tabindex="-1" aria-labelledby="enquireModalLabel" aria-hidden="true">
          <div className="modal-dialog modal-dialog-centered">
            <div className="modal-content">
              <div className="modal-body">
                <button type="button" className="btn-close" onClick={() => fnCloseModal()} style={closeBtn} data-bs-dismiss="modal" aria-label="Close"></button>
                <h5 className='text-center mb-4 px-4'>Please Enter Your Details.</h5>
                <Form id="enquireForm">
                  <div class="form-group floating">
                    <input type="text" class="form-control floating" name="name" id="name" onKeyUp={this.keyPress} required />
                    <label for="name">Name</label>
                  </div>
                  <div class="form-group floating">
                    <input type="text" class="form-control floating" name="mobile_no" id="mobile_no" onKeyUp={this.keyPress} required />
                    <label for="mobile_no">Mobile No.</label>
                  </div>
                  <div class="form-group floating">
                    <input type="text" class="form-control floating" name="email" id="email" required />
                    <label for="email">Email</label>
                  </div>
                  <p class="form_error"></p>
                  <div className="text-center">
                    <Button className='m-auto' variant="primary" type="button" onClick={() => fnSendMeDetail("enquireForm")}>
                      Send Me Detail
                    </Button>
                  </div>
                </Form>
              </div>
            </div>
          </div>
        </div>

      </>
    )
  }
}
export default Modal;