import React, { Component } from 'react';
import { Accordion, Form, Button } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faPhone, faCarOn } from '@fortawesome/free-solid-svg-icons'
import * as $ from "jquery";

class MainForm extends React.Component {

    constructor(props) {
        super(props);
        this.keyPress = this.keyPress.bind(this);
     }

     keyPress(e){
         if(e.target.id === "main_mobile_no"){
            if(/\D/g.test(e.target.value)) {
                e.target.value = e.target.value.replace(/\D/g,'');
            }
         }
         if(e.target.id === "main_name"){
            if(/[0-9]/g.test(e.target.value)) {
                e.target.value = e.target.value.replace(/[0-9]/g,'');
            }
         }
     }

    render() {

        // ----- Lead Start -----//
        const fnSendMeDetail = (form) => {
            var name = $("#" + form + " input[name=name]").val();
            var email = $("#" + form + " input[name=email]").val();
            var mobile_no = $("#" + form + " input[name=mobile_no]").val();

            var email_pattern = /^\w+([-+.'][^\s]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/;
            var mobile_length = mobile_no.length;

            if (name == "") {
                $("#" + form + " .form_error").html("Please enter name");
                return false;
            } else {
                $("#" + form + " .form_error").html("");
            }

            if (mobile_no == "") {
                $("#" + form + " .form_error").html("Please enter mobile no.");
                return false;
            } else if (mobile_length != 10) {
                $("#" + form + " .form_error").html("Please enter valid mobile no.");
                return false;
            } else {
                $("#" + form + " .form_error").html("");
            }

            if (email == "") {
                $("#" + form + " .form_error").html("Please enter email");
                return false;
            }else if (!email_pattern.test(email)) {
                $("#" + form + " .form_error").html("Please enter valid email");
                return false;
            } else {
                $("#" + form + " .form_error").html("");
            }

            $('#' + form + ' button').prop('disabled', true);

            var lead = {
                name: name,
                email: email,
                mobile: mobile_no,
                project: this.props.project,
                project_id: this.props.project_id
            };

            $.ajax({
                type: "post",
                url: 'https://mls-crm.manishalifespaces.com/api/add_lead',
                data: lead,
                success: function (data, status, jqXHR) {
                    window.location.href = '/thanks';
                },
                error: function (jqXHR, status) {
                    $('#' + form + ' button').prop('disabled', false);
                }
            });
        }
        // ----- Lead End -----//

        return (
            <>

                <Accordion className="mainForm d-none d-sm-block" defaultActiveKey="0">
                    <Accordion.Item eventKey="0">
                        <div class="mobileNo"><a href="tel:2268680097"><FontAwesomeIcon icon={faPhone} /> +91-2268680097</a></div>
                        <Accordion.Header>Contact With Us!</Accordion.Header>
                        <Accordion.Body>
                            <Form id="mainForm">
                                <div class="form-group floating">
                                    <input type="text" class="form-control floating" name="name" id="main_name" onKeyUp={this.keyPress} required />
                                    <label for="name">Name</label>
                                </div>
                                <div class="form-group floating">
                                    <input type="text" class="form-control floating" name="mobile_no" id="main_mobile_no" onKeyUp={this.keyPress} required />
                                    <label for="mobile_no">Mobile No.</label>
                                </div>
                                <div class="form-group floating">
                                    <input type="text" class="form-control floating" name="email" id="main_email" required />
                                    <label for="email">Email</label>
                                </div>
                                <p class="form_error"></p>
                                <Button variant="primary" type="button" onClick={() => fnSendMeDetail("mainForm")}>
                                    Send Me Detail
                                </Button>
                            </Form>
                            <div className="free-visit">
                                <FontAwesomeIcon icon={faCarOn} />
                                <h4>Free facility for site visit</h4>
                            </div>
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>

            </>
        )
    }
}
export default MainForm;