import React, { Component } from 'react';
import { Container, Row } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import * as $ from "jquery";
import sectionHeaderIcon from '../images/deal.png';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faPlayCircle } from '@fortawesome/free-solid-svg-icons'


class Video extends React.Component {

    render() {

        const fnOpenModal = () => {
            $('#enquireModal').addClass("show");
            $('#enquireModal').css("display","block");
            $('#enquireModal').prop("aria-hidden","false");
            $("body").append('<div class="modal-backdrop fade show"></div>');
        }

        return (
            <>

                <Container fluid className="bg-light" id="video">
                    <Row className="section-heading mb-4 mx-md-5 mx-4">
                        <img
                            className="d-block m-auto mb-2"
                            src={sectionHeaderIcon}
                        />
                        <h1 className="m-0">Virtual Site Tour</h1>
                    </Row>
                    <Row className="mx-md-5 mx-4 text-center">
                        <div className="video-img p-0 mt-3">
                            <div class="video-caption" onClick={() => fnOpenModal()}>
                                <FontAwesomeIcon icon={faPlayCircle} />
                                <h1 className='my-3'>Virtual Tour</h1>
                                <h3 className='mb-0'>Godrej Woods Phase 3 Plumeria, At Sector 43, Noida</h3>
                            </div>
                        </div>
                    </Row>
                </Container>

            </>
        )
    }
}
export default Video;